import React, { createRef } from 'react'
import gif from "./nyancat.gif"

const Math2 = require("../../../../../Other/math2")

export default class NyanCat extends React.Component{

    completedTransitions = { };

    constructor(props){
        super(props);

        this.state = { 
            animation : this.respawnAnimation(),
            rotate: 0,
            status: 1
        }
        this.topDiv = createRef()
    }

    componentDidMount(){
        setTimeout(() => this.start() , 1000)
    }

    start(){
        this.nextAnimation({ 
            x: Math2.random(-1, 1) < 0 ? -10 : 110,
            y: Math2.random(-1, 1) < 0 ? -10 : 110,
        });
    }

    getToFace(p, t){
        const vect = {
            x: p.x-t.x,
            y: -t.y - p.y }
        const angle = Math2.radToDeg(Math.atan(vect.x/vect.y));
        return {
            scale: vect.x > 0 ? -1 : 1,
            angle 
        }
    }

    continueAnimation(event){
        if(event){
            const prop = event.propertyName
            if(prop === "transform") return false;
            if(!this.completedTransitions[prop])
            {
                this.completedTransitions[prop] = true;
                if(!this.completedTransitions.left || 
                    !this.completedTransitions.top)
                    return false;
                
                this.completedTransitions = {};    
            }
        }
        return true;
    }

    nextAnimation(position, event){
        
        if(!this.continueAnimation(event)) return;

        const status = this.state.status;
        if(status === 0 && this.props.setAlive)
            this.props.setAlive(true)

        this.setState({animation: 
            status === -1 ?
            this.respawnAnimation(position) 
            : this.liveAnimation(position),
            rotate: 0, status: status + 1,
        })
    }

    liveAnimation(position){
        const speed = 100 / Math2.random(5, 10);
        
        const animation = {
            position,
            target: {
                x: Math2.random(-5, 105),
                y: Math2.random(-5, 105),
            },
            transition: `top ${speed}s, left ${speed}s`,
            scale: 1,
        };

        const rot = Math2.random(0.5, 1);
        const facing = this.getToFace(position, animation.target);

        animation.rotation = facing.angle;
        animation.scale = facing.scale;
        animation.transition += `, rotate ${rot}s`
        return animation;
    }

    onDeath(){
        this.completedTransitions = {};
        this.setState({
            animation: this.deadAnimation(),
            rotate: Math2.randomElement([-1, 1]) 
                * 360 * Math2.random(5, 8),
            status: -1,
        })
    }

    deadAnimation(){
        const speed = 5;
        const animation = {
            target: { x:Math2.random(0, 100), y:Math2.random(0, 100) },
            transition: `left ${speed}s, top ${speed}s,
             transform ${speed}s`,
            scale: 0,
        };

        return animation;
    }

    respawnAnimation(){
        const x = Math2.random(-100, 200);
        const y = x > -10 && x < 110 ? Math2.randomElement([ -10, 110 ]) : Math2.random(-200, 200);
        const speed = 100 / Math2.random(5, 10);
        const animation = {
            target: { x,  y },
            transition: `top ${speed}s, left ${speed}s`,
            scale: 1,
            hidden: true,
        };
        return animation;
    }

    getStyle(){
        const anim = this.state.animation;
        return {
            position: "absolute",
            transition: anim.transition,

            left: `${anim.target.x}%`,
            top: `${anim.target.y}%`,

            transform: (anim.scale === 0 ?
            `scale(${anim.scale}, ${anim.scale})`
            : `scale(${anim.scale}, 1)`),

            visibility: anim.hidden ? "hidden" : "visible",

            pointerEvents:"auto",
            cursor:"crosshair",
            
        }
    }

    onClick(){
        if(this.props.setAlive)
            this.props.setAlive(false)
        
        this.onDeath();
    }

    rotationStyle(){
        const r = this.state.rotate;
        return {
            transform: `rotate(${r || 0}deg)`,
            transition: `transform 5s`
        }
    }

    render(){
        return (
            <div style={this.getStyle()}
                onTransitionEnd={(event) => this.nextAnimation
                    (this.state.animation.target, event)}
                ref={this.topDiv}
                onClick={() => this.onClick() }
            >
                <div style={this.rotationStyle()}>
                    <img src={gif} width={50} style={{pointerEvents:"none"}}/>
                </div>
            </div>           
        )
    }
}