import React from 'react'

const Math2 = require("../../Other/math2")

export default class Particle extends React.Component{

    active = true;

    constructor(props){
        super(props);

        this.state = {
            colors: props.colors,
            size: props.size,
            duration: props.duration,
            offsets: props.offsets,
            style: this.retreatStyle(),
            ongoing: false,
        }
    }

    componentDidMount(){
        setTimeout(() => this.onTransitionEnd(), 1000);
    }

    setActive(bool){
        const was = this.active;
        this.active = bool;
        if(!was && bool){
            this.state.ongoing = false;
            this.nextStyle();
        }
    }

    getOrigin(){
        const origin = this.props.origin?.topDiv?.current
        if(!origin) return {  x: 0, y: 0 }
        const pos = {  x: origin.offsetLeft, y: origin.offsetTop }
        return pos;
    }

    endStyle(){
        this.setState({style:this.retreatStyle()})
    }

    nextStyle(){
        const ongoing = this.state.ongoing;
        this.setState({style:
        (ongoing ? this.retreatStyle() : this.emmitStyle()),
        ongoing: !ongoing
        })
    }

    emmitStyle(){
        const duration = this.state.duration;
        const size = Math2.random(this.state.size, this.state.size * 1.5);
        const x = Math2.random(-size * 10, size * 10)
        const y = Math2.random(-size * 10, size * 10)
        const origin = this.getOrigin();
        const offset = this.state.offsets
        const time = Math2.random(duration, duration + 1);
        return {
            backgroundColor: Math2.randomElement(this.state.colors),
            position: "absolute",
            visibility: "visible",

            left:`${origin.x + offset.x}px`, top:`${origin.y + offset.y}px`,
            opacity: 0,
            width: `${size}px`,
            height: `${size}px`,
            transform: `translate(${x}px, ${y}px)`,
            transition: `transform ${time}s, opacity ${time - 0.1}s`
        }
    }
    
    retreatStyle(){
        return {
            position: "relative",
            visibility: "hidden",
            opacity: 1,
            transform: "translate(0px, 0px)",
            transition: "transform 0.2s"
        }
    }

    onTransitionEnd(event){
        if(event?.propertyName === "opacity") return;
        if(!this.active) this.endStyle();
        else this.nextStyle();

        if(this.props.onParticleEnd)
            this.props.onParticleEnd(event)
    }

    render(){
        return (
            <div style={this.state.style}
                onTransitionEnd={(event) => this.onTransitionEnd(event)}
            >
            </div>
        )
    }
}