import React, { createRef } from 'react'
import Particles from '../../../../Particles';
import Cat from "./cat.jsx"

const Math2 = require("../../../../../Other/math2")

export default class NyanCat extends React.Component{

    completedTransitions = { };

    constructor(props){
        super(props);

        this.state = { deathAt:undefined }

        this.origin = createRef();
        this.particles = createRef();
    }

    componentDidMount(){
        this.particles.current.setOrigin(this.origin.current)
    }

    setAlive(alive){
        this.particles.current?.setActive(alive);
    }

    render(){
        return (
            <div>
                <Particles ref={this.particles} offsets={{x:25, y:25}}/>
                <Cat ref={this.origin} setAlive={(arg) => this.setAlive(arg) } />
            </div>
            
        )
    }
}