import React from 'react'
import Particle from "./particle"

export default class Particles extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            colors:     props.colors    || [ "bright red", "bright orange", "yellow", "lime", "cyan" ],
            amount:     props.amount    || 20,
            size:       props.size      || 5,
            duration:   props.duration  || 1,
            offsets:    props.offsets   || { x:0, y:0 }
        }
    }

    spawnParticles(){
        this.particles = [];
        this.references = [];
        for(let i = 0; i < this.state.amount; i++)
            this.particles.push(<Particle key={i}
                colors={this.state.colors} 
                duration={this.state.duration + i}
                size={this.state.size} 
                offsets={this.state.offsets}
                origin={this.state.origin}
                onParticleEnd={(event) => 
                    this.props.onParticleEnd && 
                    this.props.onParticleEnd(i, event) }
                
                ref={c => this.references[i] = c}
            />)
    }

    setOrigin(origin){
        this.setState({origin})
    }

    setActive(bool){
        for(const i in this.references)
            this.references[i].setActive(bool);
    }

    render(){
        if(!this.state.origin) return null;
        this.spawnParticles();
        return (
            <div>
                {this.particles}
            </div>
        )
    }
}